import React from 'react';
import TabBarBottom from '../layout/TabBarBottom';
import { CSVLink } from "react-csv";
import {CSVReader} from 'react-papaparse';
import Radium from 'radium';
import {Redirect} from 'react-router';
import $ from 'jquery';

function getContentHeight() {
    var winHeight = window.innerHeight;
    var contentHeight = winHeight-114;
    console.log(contentHeight);
    return contentHeight;
}
function About(props) {
    

    const csvStyle = {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        paddingBottom: '20px',
        //marginBottom: '10px',
    }

    const uploadStyle = {
        dropArea: {
            borderColor: 'pink',
            borderRadius: '20px',
            width: '300px',
            height: '70px',
            
          },
          dropAreaActive: {
            borderColor: 'red',
          },
          dropFile: {
            width: '300px',
            height: '150px',
            borderRadius: '20px',
            background: '#transparent',
            // border: 'solid 1px',
          },
          fileSizeInfo: {
            color: '#000',
            background: 'transparent',
            borderRadius: 3,
            lineHeight: 1,
            marginBottom: '0.6em',
            padding: '0 0.4em',
          },
          fileNameInfo: {
            color: '#000',
            background: 'transparent',
            borderRadius: 3,
            fontSize: 14,
            lineHeight: 1,
            padding: '0 0.4em',
          },
          removeButton: {
            color: '#cd2c2c',
          },
          progressBar: {
            backgroundColor: 'pink',
          },
          
    }

    const headers=[
        {label: 'ID', key: "id"},
        {label: 'Info', key: "info"}
    ]

    var data = [];

    var getAllIDs = localStorage.getItem('');
    //data.push({allIDs: getAllIDs})
    if(getAllIDs){
        var allIDsArray = getAllIDs.split('\0');
        for(let i = 0; i < allIDsArray.length-1; i++){
            var ID = allIDsArray[i];
            data.push({id: ID, info: localStorage.getItem(allIDsArray[i]).split('\0')})
        }
    }  

    const [uploadedData, setUploadedData] = React.useState([]);
    const [submitData, setSubmitData] = React.useState(false);

    /*                  CSV Reader                     */

    const handleOnDrop = (data) => {
        console.log('---------------------------')
        console.log(data)
        console.log('---------------------------')
        setUploadedData(data)
    }
    
    const handleOnError = (err, file, inputElem, reason) => {
        console.log(err)
    }
    
    const handleOnRemoveFile = (data) => {
        console.log('---------------------------')
        console.log(data)
        console.log('---------------------------')
    }

    /*******************************************************/

    const uploadData = () => {
        var currAllIDs = localStorage.getItem('')
        var currAllIDsArray = [];
        if(currAllIDs){
            currAllIDsArray = currAllIDs.split('\0');
            for(let i = 0; i < currAllIDsArray.length-1; i++){
                localStorage.removeItem(currAllIDsArray[i])
            }
        }
        
        var allIDs = "";
        for (let i = 1; i < uploadedData.length; i++){
            allIDs = allIDs + uploadedData[i].data[0] + '\0';
            localStorage.setItem(uploadedData[i].data[0],(uploadedData[i].data[1].replace(/,/g,'\0')));
        }
        localStorage.setItem('',allIDs);
        setSubmitData(true);
        props.submitDataPlants();
    }

    if(!submitData){
        return (
            <div className="aboutParent" style={{height: `${getContentHeight()}px`}}>
                <div>
                <h4 style={headerFour}>About</h4>
                <p>This web app was made to keep track of plant waterings! It saves all data locally on the browser or on the web app if saved to the homescreen.</p>

                <h4 style={headerFour}>Save To Homescreen</h4>
                <p>To maximize your experience, save this website to your homescreen and it will act like an app.</p>
                <div style={{paddingRight: '5%'}}>
                    <ul className="aboutList">
                        <li>Go to your device's default browser. Safari for IOS or Chrome for Android</li>
                        <li>Settings</li>
                        <li>Add to Home Screen</li>
                    </ul>
                </div>
                <p>All data from that point will be saved exclusively to that app. To transfer data to another device read the following: </p>
                
                
                <h4 style={headerFour}>Saving/Transfering Data</h4>
                <p>To transfer this data to another phone or another browser. First, download the data from the following link</p>
                <div style={{textAlign: 'center', marginBottom: '10px'}}>
                    <CSVLink data={data} headers={headers}>
                        <button key="download" style={styles.buttonStyle}>Download Data</button>
                    </CSVLink>
                </div>
                
                <p>Then upload that csv file to the following:</p>
                <div style={csvStyle}>
                    
                    <CSVReader
                        onDrop={handleOnDrop}
                        onError={handleOnError}
                        noDrag
                        addRemoveButton
                        onRemoveFile={handleOnRemoveFile}
                        style={uploadStyle}
                    >
                        <span>Click to upload.</span>
                    </CSVReader>
                    <button key="upload" onClick={uploadData} style={styles.buttonFlexStyle}>Submit</button>
                    
                    
                </div>
                <TabBarBottom/>
                </div>
            </div>
        )
    }
    else {
        return <Redirect push to='/'/>
    }
    

}
const headerFour = {
    textAlign: 'center',
    paddingBottom: '0',
    marginBottom: '0',
}

const styles = {
    buttonStyle: {
        background:'#ffdfef', 
        borderRadius:'5px', 
        border:'none',
        padding: '0 5px',
        height: '30px',
        ':hover': {
            background: '#fff0f7',
        }
    },
    buttonFlexStyle: {
        background:'#ffdfef', 
        borderRadius:'5px', 
        border:'none',
        padding: '0 5px',
        height: '30px',
        margin: 'auto 0 auto 5%',
        ':hover': {
            background: '#fff0f7',
        }
    },
    uploadStyle: {
        ':hover': {
            border: '#fff0f7',
        },
    }
}


export default Radium(About);
